import "../styles/checkout.css";
import { AiFillCheckCircle } from "react-icons/ai";
import whatsappLine from "../assets/images/whatsappline.jpeg";

const Checkout = () => {
  return (
    <>
      <div className="checkoutMessage">
        <div className="checkoutTitleContainer">
          <AiFillCheckCircle className="checkoutIcon" />
          <h3>Thank you for your order!</h3>

        </div>
        <span>
          Your order is being processed and will be delivered as fast as
          possible.<br />
          <i>Checkout includes delivery charges</i> <br /><br />
          <i>Make payment to the account below:</i>

          <div className="payment">
            <form action="" className="payment-form1">

              <h5 className="bank-name"> Bank Name </h5>
              <label htmlFor=""> Opay </label><br />

              <h5> Account Number </h5>
              <label htmlFor=""> 8062630897 </label> <br />

              <h5 className="bank-name"> Account Name </h5>
              <label htmlFor=""> Love Ada Chimezie </label>
            </form>
            <a className="fa" href="https://wa.me/08163369540">
              Send paymnet confirmation and location for delivery to our whatsapp line<img src={whatsappLine} alt="whatsapp" className="wapp" /> </a>
          </div>
        </span>
      </div>
    </>
  );
};

export default Checkout;
