const AdminLogin = () => {
    return (
        <>
            <div className="contactus-div">

                <form action="" className="contactus-form">
                    <p> Shawama.com </p>
                    <h4> Contact Customer Support</h4>
                    <label htmlFor=""> First Name </label>
                    <input type="text" placeholder="First Name" />

                    <label htmlFor=""> Last Name </label>
                    <input type="text" placeholder="Last Name" /><br />

                    <label htmlFor=""> Email Address </label>
                    <input type="text" placeholder=" Email Address" /><br />

                    <label htmlFor=""> Phone Number </label>
                    <input type="text" placeholder="Phone Number" /><br />

                    <label htmlFor=""> Reason for Contact </label>

                    <select className="reasonforcontact"
                        name="Choose a category">
                        <option value="All" className="alloptions" >
                            Choose your reasen for contacting us
                        </option>
                        <option className="weboptions" >Web Design </option>

                        <option value="Applications"> Applications </option>

                        <option className="webdevoptions"> Web Development </option>
                    </select>

                    <button className="contactBtn"> Contact Us </button>

                </form>
            </div>
        </>
    );
}
export default AdminLogin